export default {
	data() {
		return {
			title: 'Hello',
			query: {
				username: '',
				password: ''
			}
		}
	},
	onLoad(e) {
		if(typeof e != 'undefined'){
			uni.showToast({
				icon: 'none',
				title: e.msg
			})
		}
	},
	methods: {
		// 登录
		async commitLogin(){
			let ret = await this.$http({
				url: '/login',
				method: "POST",
				data: this.query
			})
			if (ret.code == 0){
				uni.setStorageSync('sessionId', ret.data.sessionId);
				uni.setStorageSync('userInfo', JSON.parse(ret.data.user))
				uni.switchTab({
					url: '/pages/index/index'
				})
			}else{
				uni.showToast({
					icon: 'none',
					title: ret.message
				})
			}
		},
		go_forget(){
			console.log('haha');
			uni.navigateTo({
			    url: '/pages/login/forget'
			})
		},
		go_register(){
			uni.navigateTo({
				url: '/pages/login/register'
			})
		}
		
	}
}
